$base-font-size: calc(10px + 1vmin);

.App {
  text-align: center;
  font-family: 'Roboto', serif;
}

.App-black-font {
  color: rgba(0, 0, 0, 0.75);
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.introduction-main {
  margin-top: calc(10px + 10vmin);
  width: 75vw;
  margin-left: auto;
  margin-right: auto;
}

.top-bar {
  margin-left: auto;
  margin-right: auto;
  width: 75vw;

  .theme {
    text-align: center;
    color: rgba(255, 255, 255, 0.5);
    font-size: calc(5px + 2vmin);

    &:active {
      color:white;
    }

    &:hover {
      color: rgba(255, 255, 255, 0.75);
    }
  }
}

.github-svg {
  display:inline;
}

.github-svg {
  display: inline;
  width: calc(5px + 2vmin);
  height: calc(5px + 2vmin);
  vertical-align: -0.225em;
}

.title-container {
  margin-top: 10px;
}


.article {
  @extend .App-black-font;
  margin-bottom: 50px;

  .title {
    font-size: calc(15px + 1vmin);
    font-weight: bold;
  }

  .body {
    font-size: calc(10px + 1vmin);
    .title {
      font-size: calc(12px + 1vmin);
      font-weight: bold;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  ul {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

.lang {
  font-weight: bold;
  font-size: calc(10px + 1vmin);
  color: rgba(0, 0, 0, 0.75);
}

.quote {
  position: relative;
  margin-top: 5px;
  margin-bottom: 5px;
  padding-left: 1em;
  border-left: 0.2em solid rgba(0, 0, 0, 0.3);
  font-family: 'Roboto', serif;
  color: rgba(0, 0, 0, 0.5);
  font-size: $base-font-size;
  line-height: 1em;
  font-weight: 100;
  &:before, &:after {
    content: '\201C';
    color: rgba(0, 0, 0, 0.5);
  }
  &:after {
    content: '\201D';
  }
}
